namespace fgui {
    export class GearBase {
        public static disableAllTweenEffect: boolean;
        public static enableStateUpdateEvent: boolean;

        protected _owner: GObject;
        protected _controller: Controller;
        protected _tweenConfig?: GearTweenConfig;

        public static create(owner: GObject, index: number): GearBase {
            if (!Classes)
                Classes = [
                    GearDisplay, GearXY, GearSize, GearLook, GearColor,
                    GearAnimation, GearText, GearIcon, GearDisplay2, GearFontSize
                ];
            return new (Classes[index])(owner);
        }

        constructor(owner: GObject) {
            this._owner = owner;
        }

        public dispose(): void {
            if (this._tweenConfig && this._tweenConfig._tweener) {
                this._tweenConfig._tweener.kill();
                this._tweenConfig._tweener = null;
            }
        }

        public get controller(): Controller {
            return this._controller;
        }

        public set controller(val: Controller) {
            if (val != this._controller) {
                this._controller = val;
                if (this._controller)
                    this.init();
            }
        }

        public get tweenConfig(): GearTweenConfig {
            if (!this._tweenConfig)
                this._tweenConfig = new GearTweenConfig();
            return this._tweenConfig;
        }
        public set tweenConfig(Value: GearTweenConfig) {
            this._tweenConfig = Value;
        }

        public setup(buffer: ByteBuffer): void {
            this._controller = this._owner.parent.getControllerAt(buffer.getInt16());
            this.init();

            var i: number;
            var page: string;
            var cnt: number = buffer.getInt16();

            if (this instanceof GearDisplay) {
                this.pages = buffer.readSArray(cnt);
            }
            else if (this instanceof GearDisplay2) {
                this.pages = buffer.readSArray(cnt);
            }
            else {
                for (i = 0; i < cnt; i++) {
                    page = buffer.readS();
                    if (page == null)
                        continue;

                    this.addStatus(page, buffer);
                }

                if (buffer.readBool())
                    this.addStatus(null, buffer);
            }

            if (buffer.readBool()) {
                this._tweenConfig = new GearTweenConfig();
                this._tweenConfig.easeType = buffer.readByte();
                this._tweenConfig.duration = buffer.getFloat32();
                this._tweenConfig.delay = buffer.getFloat32();
            }

            if (buffer.version >= 2) {
                if (this instanceof GearXY) {
                    if (buffer.readBool()) {
                        this.positionsInPercent = true;
                        for (i = 0; i < cnt; i++) {
                            page = buffer.readS();
                            if (page == null)
                                continue;

                            this.addExtStatus(page, buffer);
                        }

                        if (buffer.readBool())
                            this.addExtStatus(null, buffer);
                    }
                }
                else if (this instanceof GearDisplay2)
                    this.condition = buffer.readByte();
            }
        //    if (buffer.version >= 4 && this._tweenConfig != null && this._tweenConfig.easeType == EaseType.Custom) {
        //        let cnt = buffer.readInt32();
        //        console.info(`CustomEase=${cnt}`);
        //        for (let i = 0; i < cnt; i++) {
        //            let curveType = buffer.readByte();
        //            switch (curveType) {
        //                case 1:
        //                    {
        //                        let PosX = buffer.readFloat32();
        //                        let PosY = buffer.readFloat32();
        //                        let Ctrl1X = buffer.readFloat32();
        //                        let Ctrl1Y = buffer.readFloat32();
        //                        console.info(`Bezier Pos(${PosX},${PosY}) Ctrl1(${Ctrl1X},${Ctrl1Y})`);
        //                    }
        //                    break;
        //                case 2:
        //                    {
        //                        let PosX = buffer.readFloat32();
        //                        let PosY = buffer.readFloat32();
        //                        let Ctrl1X = buffer.readFloat32();
        //                        let Ctrl1Y = buffer.readFloat32();
        //                        let Ctrl2X = buffer.readFloat32();
        //                        let Ctrl2Y = buffer.readFloat32();
        //                        console.info(`Bezier Pos(${PosX},${PosY}) Ctrl1(${Ctrl1X},${Ctrl1Y}) Ctrl2(${Ctrl2X},${Ctrl2Y})`);
        //                    }
        //                    break;
        //                default:
        //                    {
        //                        let PosX = buffer.readFloat32();
        //                        let PosY = buffer.readFloat32();
        //                        console.info(`Type(${curveType}) Bezier Pos(${PosX},${PosY})`);
        //                    }
        //                    break;
        //            }
        //        }
        //    }
        }

        public updateFromRelations(dx: number, dy: number): void {

        }

        protected addStatus(pageId: string, buffer: ByteBuffer): void {

        }

        protected init(): void {

        }

        public apply(): void {
        }

        public updateState(): void {
        }
        public get owner(): GObject {
            return this._owner;
        }
    }

    var Classes: Array<typeof GearBase>;

    export class GearTweenConfig {
        public tween: boolean;
        public easeType: number;
        public duration: number;
        public delay: number;

        public _displayLockToken: number;
        public _tweener: GTweener;

        constructor() {
            this.tween = true;
            this.easeType = EaseType.QuadOut;
            this.duration = 0.3;
            this.delay = 0;
        }
    }
}