///<reference path="GearBase.ts"/>

namespace fgui {
    export class GearAnimationValue {
        playing: boolean;
        frame: number;
        public constructor(playing?: boolean, frame?: number) {
            this.playing = playing;
            this.frame = frame;
        }
        public toString(): string {
            return `${this.frame},${this.playing ? "p" : "s"}`;
        }
    }

    export class GearAnimation extends GearBase {
        private _storage: Record<string, GearAnimationValue>;
        private _default: GearAnimationValue;

        constructor(owner: GObject) {
            super(owner);
        }

        protected init(): void {
            this._default = {
                playing: this._owner.getProp(ObjectPropID.Playing),
                frame: this._owner.getProp(ObjectPropID.Frame)
            };
            this._storage = {};
        }
        public addStatusDirect(pageId: string, playing: boolean, frame: number): void {
            var gv: GearAnimationValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearAnimationValue();
            gv.playing = playing;
            gv.frame = frame;
        }
        protected addStatus(pageId: string, buffer: ByteBuffer): void {
            var gv: GearAnimationValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearAnimationValue();
            gv.playing = buffer.readBool();
            gv.frame = buffer.getInt32();
        }

        public apply(): void {
            this._owner._gearLocked = true;

            var gv: GearAnimationValue = this._storage[this._controller.selectedPageId];
            if (!gv)
                gv = this._default;

            this._owner.setProp(ObjectPropID.Playing, gv.playing);
            this._owner.setProp(ObjectPropID.Frame, gv.frame);

            this._owner._gearLocked = false;
        }

        public updateState(): void {
            var gv: GearAnimationValue = this._storage[this._controller.selectedPageId];
            let IsNew = false;
            if (!gv) {
                this._storage[this._controller.selectedPageId] = gv = new GearAnimationValue();
                IsNew = true;
            }
                
            if (GearBase.enableStateUpdateEvent) {
                let playing = this._owner.getProp(ObjectPropID.Playing);
                let frame = this._owner.getProp(ObjectPropID.Frame);
                if (IsNew || playing != gv.playing || frame != gv.frame) {
                    gv.playing = playing;
                    gv.frame = frame;
                    this._owner.displayObject.event(Events.GEAR_STATE_UPDATE, [this, this._controller.selectedPageId, gv]);
                }
            }
            else {
                gv.playing = this._owner.getProp(ObjectPropID.Playing);
                gv.frame = this._owner.getProp(ObjectPropID.Frame);
            }
        }
    }
}