namespace fgui {
    export class GearXYValue {
        x: number;
        y: number;
        px: number;
        py: number;
        public constructor(x?: number, y?: number, px?: number, py?: number) {
            this.x = x;
            this.y = y;
            this.px = px;
            this.py = py;
        }        
    }

    export class GearXY extends GearBase {
        public positionsInPercent: boolean;

        private _storage: Record<string, GearXYValue>;
        private _default: GearXYValue;

        constructor(owner: GObject) {
            super(owner);
        }

        protected init(): void {
            this._default = {
                x: this._owner.x,
                y: this._owner.y,
                px: this._owner.x / this._owner.parent.width,
                py: this._owner.y / this._owner.parent.height
            };
            this._storage = {};
        }
        public addStatusDirect(pageId: string, x: number, y: number, px: number, py: number) {
            var gv: GearXYValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearXYValue();
            gv.x = x;
            gv.y = y;
            gv.px = px;
            gv.py = py;
        }
        protected addStatus(pageId: string, buffer: ByteBuffer): void {
            var gv: GearXYValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearXYValue();
            gv.x = buffer.getInt32();
            gv.y = buffer.getInt32();
        }

        public addExtStatus(pageId: string, buffer: ByteBuffer): void {
            var gv: GearXYValue;
            if (pageId == null)
                gv = this._default;
            else
                gv = this._storage[pageId];
            gv.px = buffer.getFloat32();
            gv.py = buffer.getFloat32();
        }

        public apply(): void {
            var gv: GearXYValue = this._storage[this._controller.selectedPageId];
            if (!gv)
                gv = this._default;

            var ex: number;
            var ey: number;

            if (this.positionsInPercent && this._owner.parent) {
                ex = gv.px * this._owner.parent.width;
                ey = gv.py * this._owner.parent.height;
            }
            else {
                ex = gv.x;
                ey = gv.y;
            }

            if (this._tweenConfig && this._tweenConfig.tween && !UIPackage._constructing && !GearBase.disableAllTweenEffect) {
                if (this._tweenConfig._tweener) {
                    if (this._tweenConfig._tweener.endValue.x != ex || this._tweenConfig._tweener.endValue.y != ey) {
                        this._tweenConfig._tweener.kill(true);
                        this._tweenConfig._tweener = null;
                    }
                    else
                        return;
                }

                var ox: number = this._owner.x;
                var oy: number = this._owner.y;

                if (ox != ex || oy != ey) {
                    if (this._owner.checkGearController(0, this._controller))
                        this._tweenConfig._displayLockToken = this._owner.addDisplayLock();

                    this._tweenConfig._tweener = GTween.to2(ox, oy, ex, ey, this._tweenConfig.duration)
                        .setDelay(this._tweenConfig.delay)
                        .setEase(this._tweenConfig.easeType)
                        .setTarget(this)
                        .onUpdate(this.__tweenUpdate, this)
                        .onComplete(this.__tweenComplete, this);
                }
            }
            else {
                this._owner._gearLocked = true;
                this._owner.setXY(ex, ey);
                this._owner._gearLocked = false;
            }
        }

        private __tweenUpdate(tweener: GTweener): void {
            this._owner._gearLocked = true;
            this._owner.setXY(tweener.value.x, tweener.value.y);
            this._owner._gearLocked = false;
        }

        private __tweenComplete(): void {
            if (this._tweenConfig._displayLockToken != 0) {
                this._owner.releaseDisplayLock(this._tweenConfig._displayLockToken);
                this._tweenConfig._displayLockToken = 0;
            }
            this._tweenConfig._tweener = null;
        }

        public updateState(): void {
            var gv: GearXYValue = this._storage[this._controller.selectedPageId];
            let IsNew = false;
            if (!gv) {
                this._storage[this._controller.selectedPageId] = gv = new GearXYValue();
                IsNew = true;
            }                
            if (GearBase.enableStateUpdateEvent) {
                let x = this._owner.x;
                let y = this._owner.y;
                let px = this._owner.x / this._owner.parent.width;
                let py = this._owner.y / this._owner.parent.height;
                if (IsNew || x != gv.x || y != gv.y || px != gv.px || py != gv.py) {
                    gv.x = x;
                    gv.y = y;
                    gv.px = px;
                    gv.py = py;
                    this._owner.displayObject.event(Events.GEAR_STATE_UPDATE, [this, this._controller.selectedPageId, gv]);
                }
            }
            else {
                gv.x = this._owner.x;
                gv.y = this._owner.y;
                gv.px = this._owner.x / this._owner.parent.width;
                gv.py = this._owner.y / this._owner.parent.height;
            }            
        }

        public updateFromRelations(dx: number, dy: number): void {
            if (this._controller == null || this._storage == null || this.positionsInPercent)
                return;

            for (var key in this._storage) {
                var pt: GearXYValue = this._storage[key];
                pt.x += dx;
                pt.y += dy;
            }
            this._default.x += dx;
            this._default.y += dy;

            this.updateState();
        }
    }
}