
namespace fgui {

    export class GearText extends GearBase {
        private _storage: Record<string, string>;
        private _default: string;

        public constructor(owner: GObject) {
            super(owner);
        }

        protected init(): void {
            this._default = this._owner.text;
            this._storage = {};
        }
        public addStatusDirect(pageId: string, text: string) {
            if (pageId == null)
                this._default = text;
            else
                this._storage[pageId] = text;
        }
        protected addStatus(pageId: string, buffer: ByteBuffer): void {
            if (pageId == null)
                this._default = buffer.readS();
            else
                this._storage[pageId] = buffer.readS();
        }

        public apply(): void {
            this._owner._gearLocked = true;

            var data: any = this._storage[this._controller.selectedPageId];
            if (data !== undefined)
                this._owner.text = data;
            else
                this._owner.text = this._default;

            this._owner._gearLocked = false;
        }

        public updateState(): void {
            if (GearBase.enableStateUpdateEvent) {
                let Cur = this._storage[this._controller.selectedPageId];
                if (Cur != this._owner.text) {
                    this._storage[this._controller.selectedPageId] = this._owner.text;
                    this._owner.displayObject.event(Events.GEAR_STATE_UPDATE, [this, this._controller.selectedPageId, this._owner.text]);
                }
            }
            else {
                this._storage[this._controller.selectedPageId] = this._owner.text;
            }            
        }
    }
}