namespace fgui {
    export class GearFontSize extends GearBase {
        private _storage: Record<string, number>;
        private _default: number = 0;

        constructor(owner: GObject) {
            super(owner);
        }

        protected init(): void {
            this._default = this._owner.getProp(ObjectPropID.FontSize);
            this._storage = {};
        }
        public addStatusDirect(pageId: string, FontSize: number): void {
            if (pageId == null)
                this._default = FontSize;
            else
                this._storage[pageId] = FontSize;
        }
        protected addStatus(pageId: string, buffer: ByteBuffer): void {
            if (pageId == null)
                this._default = buffer.getInt32();
            else
                this._storage[pageId] = buffer.getInt32();
        }

        public apply(): void {
            this._owner._gearLocked = true;

            var data: any = this._storage[this._controller.selectedPageId];
            if (data != undefined)
                this._owner.setProp(ObjectPropID.FontSize, data);
            else
                this._owner.setProp(ObjectPropID.FontSize, this._default);

            this._owner._gearLocked = false;
        }

        public updateState(): void {
            if (GearBase.enableStateUpdateEvent) {
                let Cur = this._storage[this._controller.selectedPageId];
                let Value = this._owner.getProp(ObjectPropID.FontSize);
                if (Cur != Value) {
                    this._storage[this._controller.selectedPageId] = Value;
                    this._owner.displayObject.event(Events.GEAR_STATE_UPDATE, [this, this._controller.selectedPageId, Value]);
                }
            }
            else {
                this._storage[this._controller.selectedPageId] = this._owner.getProp(ObjectPropID.FontSize);
            }            
        }
    }
}