
namespace fgui {
    export class GearColorValue {
        color: string;
        strokeColor: string;
        public constructor(color?: string, strokeColor?: string) {
            this.color = color;
            this.strokeColor = strokeColor;
        }
        public toString(): string {
            if (this.strokeColor)
                return `${this.color},${this.strokeColor}`;
            else
                return `${this.color}`;
        }
    }

    export class GearColor extends GearBase {
        private _storage: Record<string, GearColorValue>;
        private _default: GearColorValue;

        public constructor(owner: GObject) {
            super(owner);
        }

        protected init(): void {
            this._default = {
                color: this._owner.getProp(ObjectPropID.Color),
                strokeColor: this._owner.getProp(ObjectPropID.OutlineColor)
            };
            this._storage = {};
        }
        public addStatusDirect(pageId: string, color: string, strokeColor: string): void {
            var gv: GearColorValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearColorValue();
            gv.color = color;
            gv.strokeColor = strokeColor;
        }
        protected addStatus(pageId: string, buffer: ByteBuffer): void {
            var gv: GearColorValue;
            if (pageId == null)
                gv = this._default;
            else
                this._storage[pageId] = gv = new GearColorValue();
            gv.color = buffer.readColorS();
            gv.strokeColor = buffer.readColorS();
        }

        public apply(): void {
            this._owner._gearLocked = true;

            var gv: GearColorValue = this._storage[this._controller.selectedPageId];
            if (!gv)
                gv = this._default;

            this._owner.setProp(ObjectPropID.Color, gv.color);
            this._owner.setProp(ObjectPropID.OutlineColor, gv.strokeColor);

            this._owner._gearLocked = false;
        }

        public updateState(): void {
            var gv: GearColorValue = this._storage[this._controller.selectedPageId];
            let IsNew = false;
            if (!gv) {
                this._storage[this._controller.selectedPageId] = gv = new GearColorValue();
                IsNew = true;
            }
            
            if (GearBase.enableStateUpdateEvent) {
                let color = this._owner.getProp(ObjectPropID.Color);
                let strokeColor = this._owner.getProp(ObjectPropID.OutlineColor);
                if (IsNew || color != gv.color || strokeColor != gv.strokeColor ) {
                    gv.color = color;
                    gv.strokeColor = strokeColor;
                    this._owner.displayObject.event(Events.GEAR_STATE_UPDATE, [this, this._controller.selectedPageId, gv]);
                }
            }
            else {
                gv.color = this._owner.getProp(ObjectPropID.Color);
                gv.strokeColor = this._owner.getProp(ObjectPropID.OutlineColor);
            }            
        }
    }
}